import { Component, OnInit, HostListener, AfterViewChecked } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Global } from '../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Beyond } from '../../../utilities/beyond';
import { environment } from '../../../environments/environment';
import { NgxSpinnerService } from 'ngx-spinner';
import { Element as StripeElement, StripeService, StripeCardComponent, ElementOptions, ElementsOptions } from "@nomadreservations/ngx-stripe";
declare var paypal: any;
declare var $: any;
@Component({
  providers: [Beyond],
  selector: 'app-create-card',
  templateUrl: './create-card.component.html',
  styleUrls: ['./create-card.component.scss']
})
export class CreateCardComponent implements OnInit {
  serProzPaymentInfoDetails: FormGroup;
  beAProValues: any;
  mobileNumber: any;
  prozDetailsData: any;
  dataProzDetails: boolean = false;
  stripeActions: boolean = false;
  showForm: boolean = false;
  firstName: any;
  lastName: any;
  prozCode: any;
  paymentType: any;
  emailId: any;
  cardDetails: any;
  accountDetails: any;
  paypalDetails: any;
  paypalResponse: any;
  serviceAreaData: any;
  cost: any;
  statesData: any;
  serviceSubcription: any;
  handler: any;
  estimatedCost: any;
  loginData: any;
  amount = 500;
  addScript: boolean = false;
  finalAmount: number = 1;
  submitted = false;
  paypalConfig = {
    env: 'sandbox',
    client: {
      sandbox: 'AclRBQkjJnhtuOndcXfrkSFBVggnO8loIskrypTfCfQ8L--nUt5xFX5IDyp-A0OsSebMUZJvEcH__etM',
      production: '<insert Production client Id>'

    },
    commit: true,
    payment: (data, actions) => {
      return actions.payment.create({
        payment: {
          transactions: [
            {
              amount: { total: this.serviceAreaData.serviceCost, currency: 'USD' }
            }
          ]
        }
      });
    },
    onAuthorize: (data, actions) => {
      return actions.payment.execute().then((payment) => {
        this.toastr.success("Payment has been Successfull...!")
      })
    }
  };


  stripeKey = '';
  error: any;
  complete = false;
  element: StripeElement;
  cardOptions: ElementOptions = {
    style: {
      base: {
        iconColor: '#666ee8',
        color: '#31325f',
        fontWeight: 400,
        fontFamily:
          '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif',
        fontSmoothing: 'antialiased',
        fontSize: '15px',
        '::placeholder': {
          color: '#aab7c4',
        },
        ':-webkit-autofill': {
          color: '#666ee8',
        },
      },
    }
  };

  elementsOptions: ElementsOptions = {
    locale: 'en'
  };

  stripeCardModalMessage = {
    header: null,
    content: null
  }

  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    private spinner: NgxSpinnerService,
    public toastr: ToastrService,
    private stripeService: StripeService,
    private _location: Location
  ) {
  }

  ngOnInit() {
    this.spinner.show()
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.serProzPaymentInfoDetails = this.formBuilder.group({
      name: ['', Validators.required],
    })
    this.prozDetails(this.activatedRoute.snapshot.params['prozId']);
    this.estimatedCost = JSON.parse(localStorage.getItem('serviceCost'))
    this.stripeService.retrievePaymentIntent('pi_1GxXdMC0mkeCkdjxV36xnpAi_secret_CpHyO3s6fWST2igxpM8ZeAHtD')
      .subscribe((result) => {
        console.log(result)
      })

  }
  //------------------NGX STRIPE PLUGIN----------------------------------------------
  prozDetails(mobileNumber) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProzDetails/' + mobileNumber, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.prozDetailsData = response.response;
          this.firstName = this.prozDetailsData[0].firstName;
          this.lastName = this.prozDetailsData[0].lastName;
          this.mobileNumber = this.prozDetailsData[0].mobileNumber;
          this.emailId = this.prozDetailsData[0].emailId;
          this.serProzPaymentInfoDetails.get('name').setValue(this.prozDetailsData[0].firstName + ' ' + this.prozDetailsData[0].lastName)
          this.showForm = true
          this.spinner.hide()
          return false
        }
      }, error => {
        console.log('The execution is failed ' + JSON.stringify(error))
      })
  }

  cardUpdated(result) {
    console.log(result)
    this.element = result.element;
    this.complete = result.card.complete;
    if (this.complete) {
      this.stripeService
    }
    this.error = undefined;
  }

  keyUpdated() {
    this.stripeService.changeKey(this.stripeKey);
  }

  async validate(type) {
    this.spinner.show()
    this.submitted = true;
    // console.log("first checking submit function")
    // stop here if form is invalid
    if (this.serProzPaymentInfoDetails.invalid) {
      // console.log("in IF checking submit function")
      return;
    }
    const response = await this.cardSetup('/cardSetup', {
      name: this.serProzPaymentInfoDetails.value.name,
      email: this.loginData.emailId,
      phone: this.loginData.mobileNumber
    });
    this.stripeService.handleCardSetup(response['client_secret'], this.element, {
      payment_method_data: {
        billing_details: {
          name: this.serProzPaymentInfoDetails.value.name,
          email: this.loginData.emailId,
          phone: this.loginData.mobileNumber
        },
      }
    }).subscribe(async (result) => {
      console.log(result)
      this.spinner.hide()
      if (type == 'TEST' && result.error) {
        this.testModeStripe(result, response)
        return false;
      }
      if (result.error && result.error.code == "incorrect_cvc") {
        this.stripeCardModalMessage.header = 'INCORRECT CVC'
        this.stripeCardModalMessage.content = 'Incorrect CVC. Please enter valid CVC.'
        $("#cardException").modal("show");
        return false;
      }
      if (result.error && result.error.code == "expired_card") {
        this.stripeCardModalMessage.header = 'CARD EXPIRED'
        this.stripeCardModalMessage.content = 'Card Expired. Please update your billing details to continue.'
        $("#cardException").modal("show");
        return false;
      }
      if (result.error && result.error.code == "card_declined" && result.error.decline_code == "generic_decline") {
        this.stripeCardModalMessage.header = 'CARD DECLINED'
        this.stripeCardModalMessage.content = 'Card Declined. Please update your billing details to continue.'
        $("#cardException").modal("show");
        return false;
      }
      if (result.error && result.error.code == "card_declined" && result.error.decline_code == "insufficient_funds") {
        this.stripeCardModalMessage.header = 'CARD DECLINED'
        this.stripeCardModalMessage.content = 'Card is Declined due to insufficient funds. Please update your billing details to continue.'
        $("#cardException").modal("show");
        return false;
      } else {
        const verifyCard = await this.creditCardSaver('/verifyProAddedCard', {
          prozId: this.activatedRoute.snapshot.params['prozId']
        })
        const cardSaved = await this.creditCardSaver('/saveStripeCard', {
          loginCode: this.activatedRoute.snapshot.params['prozId'],
          emailId: this.loginData.emailId,
          mobileNumber: this.loginData.mobileNumber,
          stripeCustomerId: response['stripeCustomerId'],
          paymentMethodString: result['setupIntent']['payment_method'],
          createdBy: this.activatedRoute.snapshot.params['prozId'],
          modifiedBy: this.activatedRoute.snapshot.params['prozId'],
          isDefaultCard: "NO",
          status: "ACTIVE",
        })
        if (cardSaved['status'] === 200) {
          this.spinner.hide()
          this.toastr.success('Card Added Successfully')
          this.router.navigate(['/leads/listLeads'])
        } else {
          this.spinner.hide()
          this.toastr.warning('Card Registration Unsuccessful')
        }
      }
    }, error => {
      this.spinner.hide()
      this.toastr.warning('Unable to connect to server')
    });
  }



  cardSetup(apiUrl, object) {
    return new Promise((resolve, reject) => {
      this.http.get(Global.url + apiUrl, object)
        .subscribe(data => {
          resolve(data.json())
        }, error => {
          resolve(error)
        })
    })
  }
  creditCardSaver(apiUrl, object) {
    return new Promise((resolve, reject) => {
      this.http.post(Global.url + apiUrl, object)
        .subscribe(data => {
          resolve(data.json())
        }, error => {
          resolve(error)
        })
    })
  }

  async testModeStripe(result, response) {
    const verifyCard = await this.creditCardSaver('/verifyProAddedCard', {
      prozId: this.activatedRoute.snapshot.params['prozId']
    })
    const cardSaved = await this.creditCardSaver('/saveStripeCard', {
      loginCode: this.activatedRoute.snapshot.params['prozId'],
      emailId: this.loginData.emailId,
      mobileNumber: this.loginData.mobileNumber,
      stripeCustomerId: response['stripeCustomerId'],
      paymentMethodString: result.error.payment_method.id,
      createdBy: this.activatedRoute.snapshot.params['prozId'],
      modifiedBy: this.activatedRoute.snapshot.params['prozId'],
      isDefaultCard: "NO",
      status: "ACTIVE",
    })
    if (cardSaved['status'] === 200) {
      this.spinner.hide()
      this.toastr.success('Card Added Successfully')
      this.router.navigate(['/leads/listLeads'])
    } else {
      this.spinner.hide()
      this.toastr.warning('Card Registration Unsuccessful')
    }
  }

  skip() {
    this._location.back();
  }

  get f() { return this.serProzPaymentInfoDetails.controls; }
  //------------------NGX STRIPE PLUGIN----------------------------------------------
  //-------------------OLD ADD CREDIT CARD DETAILS CODE------------------------------
  addPaypalScript() {
    return new Promise((resolve, reject) => {
      let scriptTagElement = document.createElement('script');
      scriptTagElement.src = "https://www.paypalobjects.com/api/checkout.js";
      scriptTagElement.onload = resolve;
      document.body.appendChild(scriptTagElement);
    })
  }
  onSubmit() {
    this.submitted = true;
    // console.log("first checking submit function")
    // stop here if form is invalid
    if (this.serProzPaymentInfoDetails.invalid) {
      // console.log("in IF checking submit function")
      return;
    }
    // console.log(" outside checking submit function")
    this.prozPaymentSave()
  }


  // For showing card details and account details starts here....
  paymentTypeSelection(x) {
    this.paymentType = x
    if (x == "PayPal") {
      this.cardDetails = false;
      this.accountDetails = false;
      this.paypalDetails = true;
      this.addPaypalScript().then(() => {
        // paypal.Button.render(this.paypalConfig, '');
      })
    }
  }

  cardDetailsRadio(x) {
    if (x == "Debit Card" || x == 'Credit Card') {
      this.cardDetails = true;
      this.accountDetails = false;
      this.paypalDetails = false;
      // this.addPaypalScript().then(() => {
      //   // paypal.Button.render(this.paypalConfig, '');
      // })
    }
  }

  netBankingRadio(x) {
    if (x == "Checking Account" || x == 'Savings Account') {
      this.cardDetails = false;
      this.accountDetails = true;
      this.paypalDetails = false;
      // this.addPaypalScript().then(() => {
      //   // paypal.Button.render(this.paypalConfig, '');
      // })
    }
  }
  // For showing card details and account details ends here....

  handlePayment() {
    this.handler.open({
      name: 'FireStarter',
      excerpt: 'Deposit Funds to Account',
      amount: this.serviceAreaData.serviceCost

    });
  }

  @HostListener('window:popstate')
  onPopstate() {
    this.handler.close()
  }


  stripePayment(token, amount) {
    this.handler.open({
    });
    var obj = {
      amount: this.amount,
      token: token
    }
    this.http.post('http://localhost:5000/charge', obj)
      .subscribe(data => {
        var response = data.json();
        console.log("This is Response: " + JSON.stringify(response))
        if (response.status === 200) {
          this.toastr.success("Payment Data Saved Successfully...!")
          this.stripeActions = true;
          this.router.navigate(['/login'])
        } else {
          this.toastr.warning('Payment Creation Failed...');
        } error => {
          console.log('The execution is failed ' + JSON.stringify(error))
        }
      })

  }


  prozPaymentSave() {
    let obj = {
      "prozId": this.activatedRoute.snapshot.params['prozId'],
      "firstName": this.firstName,
      "lastName": this.lastName,
      "mobileNumber": this.mobileNumber,
      "emailId": this.emailId,
      "creditCardNumber": this.serProzPaymentInfoDetails.value.creditCardNumber,
      "creditCardType": this.serProzPaymentInfoDetails.value.creditCardType,
      "ccvNumber": this.serProzPaymentInfoDetails.value.ccvNumber,
      "selectMonth": this.serProzPaymentInfoDetails.value.selectMonth,
      "selectYear": this.serProzPaymentInfoDetails.value.selectYear,
      "businessAddressLine1": this.serProzPaymentInfoDetails.value.businessAddressLine1,
      "state": this.serProzPaymentInfoDetails.value.state,
      "city": this.serProzPaymentInfoDetails.value.city,
      "zipCode": this.serProzPaymentInfoDetails.value.zipCode,
    }
    this.http.post(Global.url + '/addPaymentInfo', obj)
      .subscribe(data => {
        var response = data.json();
        if (response.status === 200) {
          this.toastr.success("Payment Data Saved Successfully...!")
          this.router.navigate(['/prozProfile/prozProfileConfirmation', this.activatedRoute.snapshot.params['prozId']])
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  goBack() {
    this._location.back();
  }

  //STATES
  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  //STATES
  //VIEW PROZ DETAILS
  viewProzDetails(prozId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProDetails/' + prozId, options)
      .subscribe(data => {
        var response = data.json();
        this.spinner.hide()
        if (response.status == 200) {
          let profile = response.profile[0]

        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //VIEW PROZ DETAILS
  //-------------------OLD ADD CREDIT CARD DETAILS CODE------------------------------


  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
