import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { Beyond } from "../../../utilities/beyond";
import { multiplewizard } from "../../../utilities/multiplewizard";
import { Imageselector } from "src/utilities/imageselector";
import { Http, Headers, RequestOptions } from "@angular/http";
import { ToastrService } from "ngx-toastr";
import { Global } from "../../../global/global";
import { CustomValidators } from "../../../utilities/customValidators";

const getOptions = () => {
  const headers = new Headers();
  headers.append("Accept", "application/json");
  headers.append("Content-Type", "application/json");
  return new RequestOptions({ headers: headers });
};
@Component({
  providers: [Beyond, multiplewizard, Imageselector, CustomValidators],
  selector: "app-stripe-cards",
  templateUrl: "./stripe-cards.component.html",
  styleUrls: ["./stripe-cards.component.scss"],
})
export class StripeCardsComponent implements OnInit {
  listOfCardsData: Array<any> = [];
  showData: boolean = false;
  loginData: any = {};
  cardData: any = {};
  stripeCustomerId: any;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    public imageselector: Imageselector,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.listOfCards();
  }
  //ERROR CALLBACK
  errorCallback() {
    this.toastr.warning("Unable to Connect to Server");
  }
  //ERROR CALLBACK
  //LIST OF CARDS
  listOfCards() {
    const url = Global.url + "/fetchSavedStripeCards";
    const successCallback = (data) => {
      const json = data.json();
      if (json.status == 200 && Array.isArray(json.response)) {
        this.listOfCardsData = json.response;
        this.listOfCardsData.forEach((e) => {
          if (e.isDefaultCard == 'YES') {
            this.stripeCustomerId = e.stripeCustomerId
            this.selectCard(e)
          }
        })
        this.showData = true;
      } else {
        this.showData = false;
      }
    }
    this.http
      .post(url, { loginCode: this.loginData.loginCode }, getOptions())
      .subscribe(successCallback, this.errorCallback);
  }
  //LIST OF CARDS
  //SELECT CARD
  selectCard(data) {
    if (data.stripeCustomerId == undefined || data.stripeCustomerId == '') return;
    this.stripeCustomerId = data.stripeCustomerId
  }
  //SELECT CARD
  //ADD ANOTHER CARD
  addAnotherCard() {
    this.router.navigate(['/stripe/createCard', this.loginData.loginCode])
  }
  //ADD ANOTHER CARD
  //SET DEFAULT CARD
  setDefaultCard() {
    const url = Global.url + "/setDefaultStripeCard";
    const successCallback = (data) => {
      const json = data.json();
      if (json.status == 200) {
        this.listOfCards()
        this.toastr.success('Default Card Saved Successfully')
      } else {
        this.toastr.warning('Default Card Saving Unsuccessful')
      }
    }
    this.http
      .post(url, { stripeCustomerId: this.stripeCustomerId, loginCode: this.loginData.loginCode }, getOptions())
      .subscribe(successCallback, this.errorCallback);
  }
  //SET DEFAULT CARD

}
