import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RegisterStripeComponent } from './register-stripe/register-stripe.component';
import { StripeCardsComponent } from "./stripe-cards/stripe-cards.component";
import { CreateCardComponent } from './create-card/create-card.component';
const routes: Routes = [
  {
    path: 'registerStripe',
    component: RegisterStripeComponent
  },
  {
    path: 'stripeCards',
    component: StripeCardsComponent
  },
  {
    path: 'createCard/:prozId',
    component: CreateCardComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StripeRoutingModule { }
