import { Component, OnInit } from '@angular/core';
// import { ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../global/global';
import { Beyond } from '../../../utilities/beyond';
import { Router, ActivatedRoute } from "@angular/router";
@Component({
  providers: [Beyond],
  selector: 'app-register-stripe',
  templateUrl: './register-stripe.component.html',
  styleUrls: ['./register-stripe.component.scss']
})
export class RegisterStripeComponent implements OnInit {
  stripeAuthCode: any
  proCode: any;
  stripeSuccess:boolean
  stripeFail:boolean
  loginData: any;
  constructor(
    public route: ActivatedRoute,
    public http: Http,
    public router: Router,
    public beyond: Beyond,
  ) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.proCode = JSON.parse(localStorage.getItem("login"))
    console.log("procode" + JSON.stringify(this.proCode.loginCode))
    this.route.queryParams.subscribe(params => {
      if (params.code != null || params.code != undefined) {
        var headers = new Headers();
        headers.append("Accept", 'application/json');
        headers.append('Content-Type', 'application/json');
        let options = new RequestOptions({ headers: headers });
        let obj = {
          proId: this.proCode.loginCode,
          stripeAuthCode: params.code,
          createdBy: this.proCode.loginCode,
          modifiedBy: this.proCode.loginCode,
          modifiedOn: new Date(),
          createdOn: new Date(),
          date: new Date(),
        }
        this.http.post(Global.url + '/stripeAuthCodeInsert', obj, options)
          .subscribe(data => {
            var response = data.json();
            if(response.status == 200){
              this.stripeSuccess = true
              this.stripeFail = false
            }else{
              this.stripeSuccess = false
              this.stripeFail = true
            }
          })
      }
    });
  }

  connectWithStripe() {
    window.open('https://connect.stripe.com/express/oauth/authorize?response_type=code&client_id=ca_GtZFzZHMtnbRD5RDJkEEOEKatjB7DhPI&scope=read_write#/', '_blank');
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
